<template>
  <div>
    <div class="file-input-container">
      <input type="file" id="fileInput" accept=".pdf" @change="handleFileChange" ref="fileInput" />
      <p class="file-info">(Accepted file types: .pdf)</p>
    </div>
    
    <button class="custom-card-button" @click="saveData">
      <i class="fas fa-save"></i> Save
    </button>

    <table>
      <thead>
        <tr>
          <th>No</th>
          <th>Tanggal</th>
          <th>Warta Jemaat</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in tableData" :key="index">
          <td>{{ index + 1 }}</td>
          <td>{{ formatDate(item.tanggal) }}</td>
          <td>{{ item.warta }}</td>
          <td>
            <button class="custom-card-button" @click="editData(item)">
              <i class="fas fa-edit"></i> Edit
            </button>
            <button class="delete-button" @click="deleteData(item)">
              <i class="fas fa-trash-alt"></i> Delete
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      selectedFile: null,
      tableData: [],
    };
  },
  methods: {
    async handleFileChange(event) {
      this.selectedFile = event.target.files[0];
    },
    async saveData() {
      if (this.selectedFile) {
        const currentDate = new Date().toLocaleDateString();
        const newItem = {
          tanggal: currentDate,
          warta: this.selectedFile.name,
        };

        this.tableData.push(newItem);

        this.$refs.fileInput.value = null;

        console.log('Data saved:', newItem);
      } else {
        console.log('No file selected');
      }
    },
    async getData() {
      try {
        const response = await axios.get('https://siswaapi.gkiswjateng.org/api/warta');
        this.tableData = response.data;
        console.log('Data retrieved:', this.tableData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
    formatDate(dateString) {
      const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', options);
    },
    // editData(item) {
    //   // Buka modal untuk mengedit data
    // },
    // deleteData(item) {
    //   // Hapus data dari tabel
    // },
  },
  mounted() {
    this.getData(); // Panggil fungsi getData saat komponen di-mount
  },
};
</script>

<style>
.file-input-container {
  display: flex;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 8px;
  margin-left: 25px;
}

#fileInput {
  margin-right: 8px;
}

.file-info {
  margin: 0;
}

table {
  border-collapse: collapse;
  margin: 25px;
  width: 95%;
}

thead th {
  background-color: #eee;
  padding: 5px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

tbody td {
  padding: 5px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

.delete-button {
  background-color: #ff0000;
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 5px;
  display: inline-block;
}

.custom-card-button {
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 5px;
  display: inline-block;

  margin-right: 5px;
}

.action-buttons {
  display: flex;
  justify-content: space-around;
}

.action-buttons button {
  margin: 0 4px;
  padding: 4px 8px;
  border: 1px solid #ccc;
  cursor: pointer;
}

.action-buttons button:hover {
  background-color: #eee;
}

button {
  margin-top: 8px;
  padding: 8px 16px;
  border: 1px solid #ccc;
  cursor: pointer;
}

.custom-card-button:hover {
  background-color: #248cfcce;
}

.delete-button:hover {
  background-color: #f84747d7;
}
</style>